  border: 3px solid #fff;
  box-shadow: 0 0 0 0.5rem rgba(225, 225, 225, 0.7);
}

/*-----------------------------------------------
|   Badge
-----------------------------------------------*/
.badge-rotate {
  z-index: 1;
  transform: rotate(-45deg) translate(-30%, -50%);
  position: absolute;
  padding: 0.25rem;
  padding-left: 3rem;
  padding-right: 3rem;
  left: 0;
  top: 1.8rem;
}

