|   Remodal [Language selection]
-----------------------------------------------*/
.remodal-select-language {
  max-width: 12.5rem;
  padding: 0;
}

.remodal-select-language .remodal-close {
  position: absolute;
  right: 0;
  top: 0;
  left: auto;
  padding: 0.5rem;
  cursor: pointer;
}

/*-----------------------------------------------
|   Typed Text
-----------------------------------------------*/
.typed-cursor {
  background: currentColor;
  width: 0.125rem !important;
  display: inline-block;
  text-indent: 10000rem;
  line-height: 1.4em;
}

/*-----------------------------------------------
|   Owl Carousel
-----------------------------------------------*/
.owl-theme .owl-dots {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.owl-theme .owl-dots .owl-dot {
  padding: 1rem 0;
}

.owl-theme .owl-dots .owl-dot span {
  border-radius: 0.1875rem;
  width: 3rem;
  height: 2px;
  background-color: rgba(255, 255, 255, 0.35);
  transition: background-color 0.4s ease-in-out;
}

.owl-theme .owl-dots .owl-dot.active span {
  background-color: #045bc1;
}

.owl-theme .owl-dots .owl-dot:hover span, .owl-theme .owl-dots .owl-dot:focus span {
  background-color: #404040;
}

.owl-theme .owl-nav {
  margin-top: 0;
  cursor: pointer;
}

.owl-theme .owl-nav [class*=owl-] {
  opacity: 0;
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
  font-size: modular-scale(2);
  transition: opacity 0.4s ease-in-out;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  text-align: center;
  color: #666666;
  margin: 0;
  background-color: rgba(255, 255, 255, 0.8) !important;
  box-shadow: 0.125rem 0.125rem 0.25rem rgba(0, 0, 0, 0.1);
  z-index: 99;
}

.owl-theme .owl-nav [class*=owl-]:hover, .owl-theme .owl-nav [class*=owl-]:focus {
  background-color: #fff;
  color: #666666;
  opacity: 1 !important;
}

.owl-theme .owl-nav .owl-prev {
  left: 1.8rem;
}

.owl-theme .owl-nav .owl-next {
  right: 1.8rem;
}

.owl-theme:hover .owl-nav [class*=owl-], .owl-theme:focus .owl-nav [class*=owl-] {
  opacity: 0.5;
}

.owl-theme.nav-square .owl-nav {
  margin-top: 0;
}

.owl-theme.nav-square .owl-nav [class*=owl-] {
  border-radius: 0;
}

.owl-theme.owl-nav-outer .owl-nav .owl-prev {
  left: -4rem;
}

.owl-theme.owl-nav-outer .owl-nav .owl-next {
  right: -4rem;
}

.owl-theme.owl-dots-inner .owl-dots {
  bottom: 0;
}

.owl-theme.owl-dot-round .owl-dots .owl-dot span {
  height: 0.625rem;
  width: 0.625rem;
  border-radius: 50%;
}

.owl-theme.owl-theme-info .owl-dots .owl-dot.active span {
  background-color: #00d6ff;
}

.owl-theme.owl-theme-success .owl-dots .owl-dot.active span {
  background-color: #7ed321;
}

.owl-theme.owl-theme-warning .owl-dots .owl-dot.active span {
  background-color: #f37f29;
}

.owl-theme.owl-theme-danger .owl-dots .owl-dot.active span {
  background-color: #d0021b;
}

.owl-theme.owl-eq-items .owl-stage {
  display: flex;
  flex-wrap: wrap;
}

.owl-theme.owl-eq-items .owl-item {
  display: flex;
  height: auto !important;
}

/*-----------------------------------------------
|   Owl Carousel Customization
-----------------------------------------------*/
.owl-loaded.owl-dots-inner .owl-dots {
  bottom: 1rem !important;
}

.owl-loaded .owl-dots .owl-dot span {
  position: relative;
  overflow: hidden;
  height: 0.25rem;
  width: 1.5625rem;
  display: block;
  margin: 0.5rem;
}

@media (min-width: 992px) {
  .owl-loaded .owl-dots .owl-dot span {
    width: 2.1875rem;
  }
}

@media (min-width: 1200px) {
  .owl-loaded .owl-dots .owl-dot span {
    width: 2.8125rem;
  }
}

.owl-loaded .owl-dots .owl-dot span:after {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: #fff;
  top: 0;
  left: 0;
  transform: translateX(-100%);
  border-radius: 0.1875rem;
}

.owl-loaded .owl-dots .owl-dot.active span {
  background-color: rgba(255, 255, 255, 0.35) !important;
}

.owl-loaded .owl-dots .owl-dot.active span:after {
  animation: owlNavAnimate 5s;
}

@keyframes owlNavAnimate {
  0% {
    transform: translateX(-100%);
  }
  50% {
    transform: translateX(0);
  }
  85% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(100%);
  }
}

.owl-theme.owl-theme-vertical .owl-dots {
  height: 1.125rem;
  margin-top: 0 !important;
  transform: translate3d(50%, -50%, 0) rotate(90deg);
  top: 50%;
  right: 0.5625rem;
  margin-right: -1.8rem;
  transform-origin: center;
  text-align: center;
}

.owl-theme.owl-theme-vertical.owl-dots-inner .owl-dots {
  margin-right: 1.8rem;
}

/*-----------------------------------------------
|   Only Right nav
-----------------------------------------------*/
.owl-theme-nav-right .owl-nav .owl-prev {
  display: none !important;
}

/*-----------------------------------------------
|   Height fix
-----------------------------------------------*/
.owl-carousel.h-100 .owl-item, .owl-carousel.h-100 .owl-stage, .owl-carousel.h-100 .owl-stage-outer {
